
@import "my_variables";

@media screen and (max-width: 1024px){
  .help{
    display: none;
  }

  /* The Modal (background) */
  .modal-news {
    display: table; /* Hidden by default */
    position: fixed; /* Stay in place */
    left: 0;
    top: 0;
    z-index: 300;
    padding: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.7); /* Black w/ opacity */
  }

  .hide-news{
    display: none;
  }

  /* Modal Content */
  .modal-content {
    background-color: #fefefe;
    margin: auto;
    padding: 20px;
    border: 1px solid #888;
    width: 88%;
    position: relative;
  }

  /* The Close Button */
  .close-news-modal {
    position: absolute;
    display: block;
    top: 15px;
    right: 15px;
    color: #7d7d7d;
    font-size: 28px;
    line-height: 16px;
    &:hover, &:focus{
      color: #000;
      text-decoration: none;
      cursor: pointer;
    }
  }

  ul.nav-top li:nth-child(6) {
    margin-right: 30px !important;
  }

  .keyboard-box-wrap{
    margin-left: auto;
    margin-right: auto;
  }

  .wrap-table_c-modal{
    display: table-cell;
    vertical-align: middle;
  }

  .wrap-left{
    width: 100%;
  }

  .operators-wrap {
    width: 50%;
    margin-right: 0;
  }

  .keyboard-wrap{
    display: none;
  }
  .wrap-pay-data{
    margin-bottom: 0;
  }

  .keyboard-tablet-wrap{
    width: 50%;
    min-width: 380px;
  }


  .modal-keyboard{
    right: 0 !important;
    top: -46px !important;
    .arrow-dialog:after {
      left: auto;
      bottom: -5px;
      right: 15px;
      top: auto;
    }
  }
  .modal-menu {
    top: 50px!important;
  }
  .modal-menu-li{
    top: 145px !important;
    left: 24px;
    .arrow-dialog:after {
      left: 10px;
      top: -4px;
    }
  }
}

@media screen and (max-width: 750px){
  main{
    top: 60px;
  }

  .main-wrap{
    padding-top: 0;
  }

  .nav-menu-shadow{
    background-color: rgba(0,0,0,.7);
  }

  .wrap-left{
    max-width: 300px;
    float: none;
    margin: auto;
  }

  .wrap-nav-top {
    margin-top: -8px;
  }


  ul.nav-top li {
    margin-right: 18px;
    a {
      font-size: 16px;
    }
  }

  ul.operator-list li{
    height: 50px;
     a {
       font-size: 17px;
     }
  }

  .nav-menu{
    max-width: 290px;
  }

  .keyboard-tablet-wrap{
    width: 100%;
    min-width: 300px;
  }
  .keyboard-box-wrap{
    width: 300px;
    padding-top: 28px;
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 24px;
    &:not(.bottom-tooth){
      box-shadow: none;
    }
  }

  .main-wrap{
    padding: 0;
  }

  .operators-wrap{
    clear: both;
    margin: auto;
    width: 100%;
    padding-right: 0;
    padding-top: 30px;
  }

  .key-inner{
    font-size: 26px;
  }
  .letters{
    font-size: 19px;
    padding: 0;
  }

  .no-mobile{
    display: none !important;
  }

  .only-mobile{
    display: block;
  }

  .remark.check {
    margin-top: 15px;
    margin-bottom: 15px;
  }

  .reseiver-name{
    margin-bottom: 25px;
  }

  .wrap-pay-values {
    margin-top: 20px;
    margin-bottom: 28px;
  }

  .pay-number {
    margin-top: 7px;
  }

  .wrap-pay-data {
    padding-top: 18px;
  }

  .keyboard-logos-wrap {
    margin-bottom: 19px;
  }

  .keyboard-box-wrap{
    margin-top: 25px;
  }

  .bottom-tooth{
    width: 280px;
  }
}
